/* MENI */
nav.mainmenu {
  position: relative;
  margin-left: -1em;
  margin-right: -1.03em;
  margin-top: -.5em; }
  nav.mainmenu ul {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    list-style-type: none;
    padding: 0; }
    nav.mainmenu ul li {
      background: #28a745;
      color: white;
      text-align: center;
      padding: 0.3em 0;
      font-size: 1.25rem;
      border-right: 1px solid white; }
      nav.mainmenu ul li:nth-child(5) {
        border-right: 0; }
      nav.mainmenu ul li:hover {
        background: green; }

